body {
    font-family: Arial, Helvetica, sans-serif;
    background: linear-gradient(-45deg, #ff6666, #b30000, #993333, #e6b3b3);
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

button{
    cursor: pointer;
    position: relative;
    width: 100%;
    height: 50px;
    background: #63A063;
    border: 0;
    color: #fff;
    font-weight: bold;
    border-radius: 20px;
    box-shadow:
            inset 0 0 0 1px #2B862B,
            inset 0 0 0 2px rgba(255,255,255, .15),
            0 8px 0 0 #648052,
            0 8px 0px 1px rgba(0,0,0,.5),
            0 8px 8px 1px rgba(0,0,0,.5);
}
button:hover,
button:focus{
    outline: 0;
    top: 2px;
    background: #63A063;
    -webkit-transition: 0.2s ease-in-out;
    -moz-transition: 0.2s ease-in-out;
    -o-transition: 0.2s ease-in-out;
    transition: 0.2s ease-in-out;
    box-shadow:
            inset 0 0 0 1px #2B862B,
            inset 0 0 0 2px rgba(255,255,255, .15),
            0 6px 0 0 #648052,
            0 6px 0px 1px rgba(0,0,0,.5),
            0 6px 8px 1px rgba(0,0,0,.5);
}
button:active{
    outline: 0;
    top: 8px;
    box-shadow:
            inset 0 0 0 1px #2B862B,
            inset 0 0 0 2px rgba(255,255,255, .15),
            0 1px 0 0 #648052,
            0 1px 0px 1px rgba(0,0,0,.5),
            0 1px 8px 1px rgba(0,0,0,.5);
}

input {
    width: 100%;
}

:root {
    /* generic */
    --gutterSm: 0.4rem;
    --gutterMd: 0.8rem;
    --gutterLg: 1.6rem;
    --gutterXl: 2.4rem;
    --gutterXx: 7.2rem;
    --colorPrimary400: #7e57c2;
    --colorPrimary600: #5e35b1;
    --colorPrimary800: #4527a0;
    --fontFamily: "Dosis", sans-serif;
    --fontSizeSm: 1.2rem;
    --fontSizeMd: 1.6rem;
    --fontSizeLg: 2.1rem;
    --fontSizeXl: 2.8rem;
    --fontSizeXx: 3.6rem;
    --lineHeightSm: 1.1;
    --lineHeightMd: 1.8;
    --transitionDuration: 300ms;
    --transitionTF: cubic-bezier(0.645, 0.045, 0.355, 1);

    /* floated labels */
    --inputPaddingV: var(--gutterMd);
    --inputPaddingH: var(--gutterLg);
    --inputFontSize: var(--fontSizeLg);
    --inputLineHeight: var(--lineHeightMd);
    --labelScaleFactor: 0.8;
    --labelDefaultPosY: 50%;
    --labelTransformedPosY: calc(
            (var(--labelDefaultPosY)) -
            (var(--inputPaddingV) * var(--labelScaleFactor)) -
            (var(--inputFontSize) * var(--inputLineHeight))
    );
    --inputTransitionDuration: var(--transitionDuration);
    --inputTransitionTF: var(--transitionTF);
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

html {
    height: 100%;
    font-size: 10px;
}

body {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #455A64;
    background-color: #E1F0EC;
    font-family: var(--fontFamily);
    font-size: var(--fontSizeMd);
    line-height: var(--lineHeightMd);
    border: 2px solid #ffffff;
    border-radius: 30px;
}

input {
    width: 100%;
    padding: 15px 12px;
    margin-bottom: 5px;
    border: 1px solid #e5e5e5;
    border-bottom: 2px solid #ddd;
    background: #f2f2f2;
    color: #555;
}

.login-wrapper {
    flex-direction: row !important;
}

form {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

form, h1 {
    text-align: center;
    color: #ffffff;
    letter-spacing: 5px;
    font-weight: 100;
    transition: all 1s;
    font-size: 30px;
}

@media only screen and (min-width: 960px) {
    form, h1 {
        letter-spacing: 12px;
        font-size: 40px;
    }
}

